class FetchExample extends React.Component {
    
    state = {
        counter: 0
    };

    onClick = () => {
        this.setState({counter: this.state.counter + 1});
        document.title = `You click ${this.state.counter} times!`;
    };

    render() {
        return (
            <div>
                <p onClick={this.onClick}>Click me!</p>
            </div>
        )
    }

}

export default FetchExample;
